.text-large{
    font-size: 50px;
}

.left-border{
    border-left: 0.5rem solid #0d6efd;
}

.project-card {
	 --d: 700ms;
	 --e: cubic-bezier(0.19, 1, 0.22, 1);
}
.page-content {
	 display: grid;
	 grid-gap: 1rem;
	 padding: 1rem;
	 max-width: 1024px;
	 margin: 0 auto;
	 font-family: var(--font-sans);
}
 @media (min-width: 600px) {
	 .page-content {
		 grid-template-columns: repeat(2, 1fr);
	}
}
 @media (min-width: 800px) {
	 .page-content {
		 grid-template-columns: repeat(4, 1fr);
	}
}

 .card-img {
	 position: relative;
	 display: flex;
	 overflow: hidden;
	 padding: 1rem;
	 width: 100%;
	 text-align: center;
	 color: whitesmoke;
	 background-color: whitesmoke;
}
 @media (min-width: 600px) {
	 .card-img {
		 height: 350px;
	}
}
 .card-img:before {
	 content: '';
	 position: absolute;
	 top: 0;
	 left: 0;
	 width: 100%;
	 height: 110%;
	 background-size: cover;
	 background-position: 0 0;
	 transition: transform calc(var(--d) * 2.5) var(--e);
	 pointer-events: none;
}
 .card-img:after {
	 content: '';
	 display: block;
	 position: absolute;
	 top: 0;
	 left: 0;
	 width: 100%;
	 height: 200%;
	 pointer-events: none;
	 background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.762) 50%, rgba(0, 0, 0, 0.790) 100%);
	 transform: translateY(-50%);
	 transition: transform calc(var(--d) * 2) var(--e);
}
 .card-img:nth-child(1):before {
	 background-image: url(../../images/assects/drive_to_park.png)
}
 .card-img:nth-child(2):before {
	 background-image: url(../../images/assects/drive_to_park3.png);
}
 .card-img:nth-child(3):before {
	 background-image: url(../../images/assects/react_youtube.png);
}
.card-img2:nth-child(1):before {
	background-image: url(../../images/assects/drive_to_park2.png)
}
.card-img2:nth-child(2):before {
	background-image: url(../../images/assects/drive_to_park4.png);
}
.card-img2:nth-child(3):before {
	background-image: url(../../images/assects/react_youtube.png);
}
 .content {
	 position: relative;
	 display: flex;
	 flex-direction: column;
	 align-items: center;
	 width: 100%;
	 padding: 1rem;
	 transition: transform var(--d) var(--e);
	 z-index: 1;
}
 .content > * + * {
	 margin-top: 1rem;
}
 .title {
	 font-size: 1.3rem;
	 font-weight: bold;
	 line-height: 1.2;
}
 .copy {
	 font-size: 1.125rem;
	 font-style: italic;
	 line-height: 1.35;
}
 @media (hover: hover) and (min-width: 600px) {
	 .card:after {
		 transform: translateY(0);
	}
	 .content {
		 transform: translateY(calc(100% - 4.5rem));
	}
	 .content > *:not(.title) {
		 opacity: 0;
		 transform: translateY(1rem);
		 transition: transform var(--d) var(--e), opacity var(--d) var(--e);
	}
	 .card:hover, .card:focus-within {
		 align-items: center;
	}
	 .card:hover:before, .card:focus-within:before {
		 transform: translateY(-10%);
	}
	 .card:hover:after, .card:focus-within:after {
		 transform: translateY(-50%);
	}
	 .card:hover .content, .card:focus-within .content {
		 transform: translateY(0);
	}
	 .card:hover .content > *:not(.title), .card:focus-within .content > *:not(.title) {
		 opacity: 1;
		 transform: translateY(0);
		 transition-delay: calc(var(--d) / 5);
	}
	 .card:focus-within:before, .card:focus-within:after, .card:focus-within .content, .card:focus-within .content > *:not(.title) {
		 transition-duration: 0s;
	}
}

.square{
	position: absolute;
	width: 100px;
	height: 100px;
	left: 10%;
	background-color: transparent;
	border: 5px solid orange;
	box-shadow: 2px black;
	z-index: 100;
}
.square {
	offset-path: path('M20,20 C20,400 400,0 00,500');
	animation: move 5000ms infinite alternate ease-in-out;
	animation-delay: 3000ms;
  }
  
  @keyframes move {
	0% {
	  offset-distance: 0%;
	}
	50% {
	  offset-distance: 50%;
	}
	100% {
		offset-distance: 100%;
	}
  }

